@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,900;1,300;1,400;1,900&display=swap";
body {
  color: #7fff00;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 18px;
}

.historyframe {
  width: 100%;
  margin-bottom: 50px;
}

.typeframe {
  width: 100%;
  background-color: #000;
  border-top: 1px solid #7fff00;
  flex-grow: 1;
  position: fixed;
  bottom: 0;
  left: 0;
}

.typearea {
  color: #fff;
  outline: 0;
  padding: 15px;
}

.mainframe {
  padding: 15px;
}

.overlay {
  width: 100%;
  height: 100vh;
  background: url("back.65faf737.jpg") 0 0 / cover;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.welcome {
  background-color: #000c;
  border: 4px dotted #7fff00;
  padding: 15px;
}

.welcome h1 {
  font-size: 18px;
  font-weight: 400;
}

.welcome-header {
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px;
  display: flex;
}

.logform {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.logform-btn {
  color: #000;
  background-color: #7fff00;
  border: 0;
  padding: 10px;
  font-family: inherit;
  font-size: 16px;
}

.logform-input {
  height: 26px;
  color: #fff;
  background-color: #0000;
  border: 1px solid #7fff00;
  outline: 0;
  font-family: inherit;
  font-size: 16px;
}

.hidden {
  display: none;
}

.logo {
  color: #fff;
  width: 300px;
  background-color: #0000;
  font-size: 45px;
  font-weight: 900;
  position: fixed;
  top: 10px;
  left: 10px;
}

.login, .getname {
  width: 100%;
  height: 100vh;
  background: url("back.65faf737.jpg") 0 0 / cover;
  position: fixed;
}

.ava {
  width: 70px;
  height: 70px;
  border: 0;
  border-radius: 50%;
  position: fixed;
  bottom: 15px;
  right: 15px;
  overflow: hidden;
}

.loginMsg {
  width: 80%;
  max-width: 300px;
  color: #000;
  background-color: #f0f8ff;
  border-radius: 20px 20px 0;
  font-size: 14px;
  position: fixed;
  bottom: 60px;
  right: 10px;
}

.container {
  padding: 15px;
}

.js-link {
  color: #8a2be2;
  font-size: 12px;
}

.nameForm__input {
  width: 150px;
  background-color: #0000;
  border: 0;
  outline: none;
  padding: 7px;
  font-family: inherit;
}

.nameForm {
  width: 100%;
  display: flex;
}

.nameForm__btn {
  background-color: #0000;
  border: 0;
  font-family: inherit;
}

